import { registerBlockType } from "@wordpress/blocks";
import { Fragment } from "@wordpress/element";
import { InnerBlocks, useBlockProps } from "@wordpress/block-editor";
import { Placeholder } from '@wordpress/components';

import './style.scss';
import './editor.scss';

const accordionTemplate = [
  [ 'core/heading', { level: 2, className: 'playbook-accordion-header alignwide', placeholder: 'Haitarilistauksen otsikko h2' } ],
  [ 'playbook/accordion-item', {}],
];

registerBlockType("playbook/accordion", {
  title: "accordion",
  icon: "list-view",
  category: "playbook",
 
  edit: props => {
    const {
      className,
    } = props;
    const blockProps = useBlockProps();
    const ALLOWED_BLOCKS = [ 'playbook/accordion-item', 'core/separator' ];

    return (
      <section { ...blockProps } className="playbook-accordion">
        <InnerBlocks
          template={ accordionTemplate }
          allowedBlocks={ ALLOWED_BLOCKS }
        />
      </section>
    );
  },
  save: props => {;
    const blockProps = useBlockProps.save();
    return (
      <section { ...blockProps } className="playbook-accordion" >
          <div>
            <InnerBlocks.Content />
          </div>
      </section>
    );
  }
});