import { registerBlockType } from "@wordpress/blocks";
import { Fragment } from "@wordpress/element";

const { RichText, InspectorControls } = wp.blockEditor;
const { PanelBody, PanelRow, ColorPicker } = wp.components;

registerBlockType("playbook/accordion-item-header", {
  title: "accordion-item-header",
  icon: "heading",
  category: "playbook",
  parent: [ "playbook/accordion-item" ],
  attributes: {
    content: {
      source: "html",
      selector: "h3"
    },
    bgColor: {
      type: "object",
      default: '#fafafb'
    },
    textColor: {
      type: "object",
      default: '#000000'
    },
    blockId: {
      type: "string",
    },
    nextBlockId: {
      type: "string",
    },
  },
 
  edit: props => {
    const {
      attributes,
      setAttributes,
      className,
      clientId
    } = props;
    const { content, textColor, bgColor, nextBlockId } = attributes;
    console.log(" props", props)

    // const { clientId } = this.props;
    // const { select } = wp.data;

    // const parentBlock = select( 'core/editor' ).getBlocksByClientId( clientId )[ 0 ];
    // const childBlocks = parentBlock.innerBlocks;
    // console.log(childBlocks[ 0 ].attributes);

    const parentBlocks = wp.data.select( 'core/block-editor' ).getBlockParents(props.clientId); 
    const parentAttributes = wp.data.select('core/block-editor').getBlocksByClientId(parentBlocks);
    const next = wp.data.select('core/block-editor').getAdjacentBlockClientId(props.clientId);
    console.log("Heading parent", parentAttributes);
    console.log("HEading next", next);
    setAttributes({ blockId: clientId });
    setAttributes({ nextBlockId: next });

    return ( 
      <Fragment>
        <InspectorControls>
          <PanelBody
            title="Playbook Accordion settings"
            initialOpen={true}
          >
            <PanelRow>
              <p>Otsikon taustaväri</p>
            </PanelRow>
            <PanelRow>
              <ColorPicker
                color={bgColor}
                onChangeComplete={(newval) => setAttributes({ bgColor: newval.hex })}
                disableAlpha
              />
            </PanelRow>

            <PanelRow>
              <p>Tekstin väri</p>
            </PanelRow>
            <PanelRow>
              <ColorPicker
                color={textColor}
                onChangeComplete={(newval) => setAttributes({ textColor: newval.hex })}
                disableAlpha
              />
            </PanelRow>

          </PanelBody>
        </InspectorControls>
        <RichText
          tagName="h3"
          value={content}
          className="playbook-accordion-item-header"
          placeholder="Haitarielementin otsikko H3"
          role="heading" 
          aria-level="3"  
          tab-index="0"
          style={{
            color: textColor,
            backgroundColor: bgColor,
          }}
          onChange={newContent => {
            setAttributes({ content: newContent });
          }}
        />
      </Fragment>
    );
  },
  save: props => {
    const {
      attributes,
      setAttributes,
      className
    } = props;
    const { content, textColor, bgColor, blockId, nextBlockId } = attributes;

    return (
      
      <button 
        type="button" 
        aria-expanded="false" 
        className="playbook-accordion-item-header"
        data-bar="bar"
        id={`block-${blockId}`}
        aria-controls={`block-${nextBlockId}`}
        style={{
          color: textColor,
          backgroundColor: bgColor,
        }}
      >
        <RichText.Content
        tagName="h3"
        // className="alignwide"
        value={content}
        aria-label="title for playbook-accordion-item content"
        role="heading" 
        aria-level="3" 
        tab-index="0"
      />
      </button>
    );
  }
});