const { __ } = wp.i18n;
import { registerBlockType } from "@wordpress/blocks";
import { Fragment } from "@wordpress/element";
import { InnerBlocks, useBlockProps } from "@wordpress/block-editor";
import { Button } from '@wordpress/components';
import { MediaUpload, MediaUploadCheck } from '@wordpress/block-editor';
import { addFilter } from '@wordpress/hooks';
const { RichText, InspectorControls } = wp.blockEditor;
const { ToggleControl, PanelBody, PanelRow, CheckboxControl, SelectControl, ColorPicker, Spinner, ResponsiveWrapper } = wp.components;

import './style.scss';


const featuredTemplate = [
  [ 'core/group', { className: 'playbook-featured' }, [
    [ 'core/columns', {
      columns: 3,
    }, [
      [ 'core/column', {}, [
        [ 'core/group', { className: 'featured-content' }, [
          [ 'core/heading', { 
            level: 2, 
            className: 'featured-header',
            placeholder: 'Featured title' } ],
          [ 'core/paragraph', {placeholder: "Featured content"}],
        ] ],
      ] ],
      [ 'core/column', {}, [
        [ 'core/image', {}],
      ] ],
    ] ],
  ] ],
];

registerBlockType("playbook/featured", {
  title: "featured",
  icon: "align-pull-right",
  category: "playbook",
  supports: { align: true },
  attributes: {
    myRichHeading: {
      type: 'string',
    },
    myRichText: {
      type: 'string',
      source: 'html',
      selector: 'p'
    },
    toggle: {
      type: 'boolean',
      default: true
    },
    favoriteAnimal: {
      type: 'string',
      default: 'dogs'
    },
    bgColor: {
      type: 'string',
      default: '#DDDDDD'
    },
    activateLasers: {
      type: 'boolean',
      default: false
    },
    bgImageId: {
        type: 'number',
        default: 1020
    },
    bgImageUrl: {
        type: 'string',
        default: 'https://eqsign.fi/test/wp-content/themes/playbook/assets/images/waves-green.svg',
    },
  },
 
  edit: props => {
    const {
      className, supports, attributes, setAttributes} = props;
    props.setAttributes({ align: 'wide' });
    const blockProps = useBlockProps();
    // const ALLOWED_BLOCKS = [ 'playbook/hero-content', 'core/separator' ];
    const ALLOWED_MEDIA_TYPES = [ 'image' ];
    const { bgImageId, bgImageUrl, bgColor } = attributes;
    const onUpdateImage = ( image ) => {
      setAttributes( {
        bgImageId: image.id,
        bgImageUrl: image.url,
      } );
    };
    const onRemoveImage = () => {
      setAttributes( {
        bgImageId: undefined,
        bgImageUrl: undefined,
      } );
    };
    const instructions = <p>{ __( 'To edit the background image, you need permission to upload media.', 'playbook' ) }</p>;

    // console.log("bgImageUrl: ", bgImageUrl);

    return (
      <Fragment>
        <InspectorControls>
          <PanelBody
            title="Playbook featured settings"
            initialOpen={true}
          >
            <PanelRow>
              <div className="wp-block-featured-bg-image-selector">

                <MediaUploadCheck fallback={ instructions }>
                  <MediaUpload
                    title={ __( 'Background image', 'playbook' ) }
                    onSelect={ onUpdateImage }
                    allowedTypes={ ALLOWED_MEDIA_TYPES }
                    value={ bgImageId }
                    render={ ( { open } ) => (
                      <Button
                        className={ ! bgImageId ? 'editor-post-featured-image__toggle' : 'editor-post-featured-image__preview' }
                        onClick={ open }>
                        { ! bgImageId && ( __( 'Set background image', 'playbook' ) ) }
                        { !! bgImageId && ! bgImageUrl && <Spinner /> }
                        { bgImageUrl &&
                          <img src={ bgImageUrl } alt="featured background" />
                        }
                      </Button>
                    ) }
                  />
                </MediaUploadCheck>

                { !! bgImageId && bgImageUrl &&
                  <MediaUploadCheck>
                    <MediaUpload
                      title={ __( 'Background image', 'playbook' ) }
                      onSelect={ onUpdateImage }
                      allowedTypes={ ALLOWED_MEDIA_TYPES }
                      value={ bgImageId }
                      render={ ( { open } ) => (
                        <Button onClick={ open } isDefault isLarge>
                          { __( 'Replace background image', 'playbook' ) }
                        </Button>
                      ) }
                    />
                  </MediaUploadCheck>
                }
                { !! bgImageId &&
                  <MediaUploadCheck>
                    <Button onClick={ onRemoveImage } isLink isDestructive>
                        { __( 'Remove background image', 'playbook' ) }
                    </Button>
                  </MediaUploadCheck>
                }
              </div>
            </PanelRow>
            
            <PanelRow>
              <p>taustaväri</p>
            </PanelRow>
            
            <PanelRow>
              <ColorPicker
                color={attributes.bgColor}
                onChangeComplete={(newval) => setAttributes({ bgColor: newval.hex })}
                disableAlpha
              />
            </PanelRow>

            {/* <PanelRow>
              <ToggleControl
                label="Toggle me"
                checked={attributes.toggle}
                onChange={(newval) => setAttributes({ toggle: newval })}
              />
            </PanelRow>
            <PanelRow>
              <SelectControl
                label="What's your favorite animal?"
                value={attributes.favoriteAnimal}
                options={[
                  {label: "Dogs", value: 'dogs'},
                  {label: "Cats", value: 'cats'},
                  {label: "Something else", value: 'weird_one'},
                ]}
                onChange={(newval) => setAttributes({ favoriteAnimal: newval })}
              />
            </PanelRow>
            
            <PanelRow>
              <CheckboxControl
                label="Activate lasers?"
                checked={attributes.activateLasers}
                onChange={(newval) => setAttributes({ activateLasers: newval })}
              />
            </PanelRow> */}

          </PanelBody>
        </InspectorControls>

        <div 
          { ...blockProps } 
          className={ 'alignWide wp-block-playbook-featured' }
        >
          <div 
            className="featured"
            style={{
              backgroundImage: `url(${ bgImageUrl })`,
              backgroundColor: bgColor
            }}
          >
            <InnerBlocks
              template={ featuredTemplate }
            />
          </div>
        </div>
      </Fragment>
    );
  },
  save: props => {
    const { className, supports, attributes, setAttributes} = props;
    const { bgImageUrl, bgColor } = attributes;
    const blockProps = useBlockProps.save();

    return (
      <div 
        { ...blockProps } 
        >
          <div 
            className="featured" 
            style={{
              backgroundImage: `url(${ bgImageUrl })`,
              backgroundColor: `${ bgColor }`
            }} 
          >
            <InnerBlocks.Content />
          </div>
      </div>
    );
  }
});