import { registerBlockType } from "@wordpress/blocks";
import { InnerBlocks, useBlockProps } from "@wordpress/block-editor";
import { Fragment } from "@wordpress/element";

const accordionItemTemplate = [
  [ 'core/group', { className: 'playbook-accordion-item' }, [
    [ 'playbook/accordion-item-header', { className: 'playbook-accordion-item-header'} ],
    [ 'playbook/accordion-item-content', { className: 'playbook-accordion-item-content'} ],
  ] ],
];

registerBlockType("playbook/accordion-item", {
  title: "accordion-item",
  icon: "media-document",
  category: "playbook",
  parent: [ "playbook/accordion" ],
  attributes: {
      blockId: {
          type: 'string',
      }
  },
 
  edit: props => {
    const {
      attributes,
      setAttributes,
      clientId
    } = props;
    const { blockId } = attributes;
    console.log(clientId)

    setAttributes({ blockId: clientId });

    const { select } = wp.data;

    const parentBlock = select( 'core/editor' ).getBlocksByClientId( clientId )[ 0 ];
    const childBlocks = parentBlock.innerBlocks;
    console.log("Item has:", childBlocks);
   

    const blockProps = useBlockProps();

    return (
      <article { ...blockProps } >
        <InnerBlocks
          template={ accordionItemTemplate }
          templateLock="all"
           foo="foo"
        />
      </article>
    );
  },
  save: props => {
    const {
      attributes,
    } = props;
    const { blockId } = attributes;
    const blockProps = useBlockProps.save();
    return (
      <article { ...blockProps } className="playbook-accordion-item" role="region"  id={`accordion-item-${blockId}`}>
        <InnerBlocks.Content />
      </article>
    );
  }
});