import { registerBlockType } from "@wordpress/blocks";
import { InnerBlocks, useBlockProps } from "@wordpress/block-editor";

const accordionItemContentTemplate = [
    [ 'core/group', { className: 'accordion-item-content-content alignwide' } ],
  ]

registerBlockType("playbook/accordion-item-content", {
  title: "accordion-item-content",
  icon: "media-document",
  category: "playbook",
  parent: [ "playbook/accordion-item" ],
 
  edit: props => {
    const {
      clientId
    } = props;
    
    console.log("content", clientId);

    const blockProps = useBlockProps();
    return (
      <div { ...blockProps }>
        <InnerBlocks
          template={ accordionItemContentTemplate }
          templateLock={ false }
        />
      </div>
    );
  },
  save: props => {
    const blockProps = useBlockProps.save();
    return (
      <div { ...blockProps } className="playbook-accordion-item-content">
        <InnerBlocks.Content />
      </div>
    );
  }
});